@import url("https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Jost", sans-serif;
}

html {
  font-size: 62.5%;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}


.main-nav{
    width: 100%;
//  border: 1px solid red;
    background: white;
    // height: 70px;
    position: fixed;
    display: grid;
    grid-template-columns: 5rem 1fr   4fr 0.5fr 5rem;
    justify-content: space-between;

    box-sizing: border-box;
    z-index: 1300;
}
.dark-mode{
  width: 100%;
/* position: absolute; */
  /* height:10rem ; */
  /* border: 1px solid green; */
  background-color: rgb(26,32,44);
  display: grid;
  grid-template-columns: 5rem 1fr   4fr 0.5fr 5rem;
  justify-content: space-between;
 position: fixed;
  box-sizing: border-box;
  z-index: 1000;
  .resumeBTN{
    background-color: red;
  }
}

.logo{
    display: grid;
    /* border: 1px solid red; */
    grid-column: 2/3;
    justify-content: start;
    align-items: center;
    box-sizing: border-box;
}
.menu-link {
    grid-column: 3/4;
    /* border: 1px solid green; */
    /* display: flex;
    margin: auto;
    gap: 20px; */
    padding-left: 30%;
    justify-content: space-around;
  }
  .menu-link ul {
    /* height: 10rem; */
    display: flex;
    margin-top: 1rem;
    
    justify-content: space-around;
    align-items: center;
  }
  .menu-link ul li{
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 5px;
    /* border: 1px solid red; */
    font-family: Poppins, sans-serif;
  }
  .menu-link ul li:hover{
    text-decoration: underline;
    cursor: pointer;
    color: rgb(243, 16, 16);
  }
  .social-media {
    grid-column: 4/5;
  }
  
.logo h2 {
    font-size: 4.5rem;
   font-family: Poppins, sans-serif;
    font-weight: 650;
    margin: auto;
    cursor: pointer;
    justify-content: center;
    align-content: center;
    /* text-transform: uppercase; */
    /* background: -webkit-linear-gradient(#eee, rgb(69, 57, 248)); */
   
  }
  
  .logo h2 span {
    color: red;
    font-weight: 600;
    font-size: 3.5rem;
  }
  
  .menu-link ul li {
    font-size: 1.8rem;
  }
  
  .menu-link ul li a {
    text-transform: capitalize;
    color: rgba(0, 0, 3, 0.8);
    transition: 0.5s;
  }
  .menu-link ul li:hover > a {
    transform-origin: left;
    color: rgba(0, 0, 3, 1);
    text-decoration: underline;
    transition: 0.5s;
  }
  .social-media{
    font-size: 1.8rem;
  }
  .social-media .hamburger-menu {
    display: none;
  }
  .social-media ul {
    /* height: 10rem; */
    margin-top: 1.7rem;
    display: grid;
    grid-template-columns: 3fr repeat(3, 1fr);
    align-items: center;
    justify-content: flex-end;
  }
  
  .social-media ul li {
    text-align: right;
  }
  
  .social-media ul li:first-child {
    grid-column: 2/3;
  }










  /* responsive css style  */
@media (max-width: 1080px) {
    .main-nav {
      height: 8rem;
      grid-template-columns: 2rem 3fr 3fr 1fr 2rem;
    }
    .dark-mode{
      height: 8rem;
      grid-template-columns: 2rem 3fr 3fr 1fr 2rem;
    }
    .menu-link{
        padding-left: 30%;
    }  
    .logo,
    

  
    .menu-link ul,
    .social-media ul {
      height: 8rem;
    }
  }
  
  /* responsive css style  */
  @media (max-width: 998px) {
    .main-nav {
      height: 7rem;
      grid-template-columns: 2rem 2fr 3fr 2rem 2rem;
    }
    .dark-mode{
      height: 7rem;
      grid-template-columns: 2rem 2fr 3fr 2rem 2rem;
    }
    .menu-link {
      display: none;
    }
  
    .logo,
    .social-media ul {
      height: 7rem;
    }
  
    .mobile-menu-link {
      grid-column: 2/4;
      position: relative;
      z-index: 99;
      padding: 25px 0px;
      color: black;
      background: rgba(210, 201, 201, 0.5);
  // background-color: white;  
box-shadow: 0 10px 25px rgba(0,0,0,0.2);
backdrop-filter: blur(15px);
      // background: "rgb(1, 75, 97)";
      // background: radial-gradient(circle, rgba(186,180,180,1) 17%, rgba(145,145,145,1) 88%);
            height: auto;
            width: 80%;
            display: grid;
            grid-column: 2/5;
            border: none;
            align-items: center;
            padding-left: 3rem;
            transition: all 2s linear;
            transform-origin: top;
      border-radius: 20px;
    }
    .dark-mode{
      .mobile-menu-link {
        grid-column: 2/4;
        position: relative;
        z-index: 99;
        padding: 25px 0px;
        color: white;
        border-radius: 20px;
      }
    }
    
    .mobile-menu-link ul {
      height: 20rem;
      display: flex;
      justify-content: space-around;
      flex-direction: column;
      align-items: start;
    }
  
    .mobile-menu-link ul li:first-child {
      transition-delay: 0.2s;
    }
  
    .social-media {
      grid-row: 1/2;
      grid-column: 3/5;
      justify-items: end;
      align-items: center;
      transition: all 2s linear;
    }
  
    .social-media .social-media-desktop {
      height: 0;
      display: none;
    }
  
    .social-media {
      height: 7rem;
      display: flex;
      justify-self: end;
      align-items: center;
    }
  
    .social-media .hamburger-menu {
      display: block;
      font-size: 2.5rem;
    }
    .colorModeSwap{
      /* border: 1px solid rgb(72, 30, 226); */
     /* width: 45px; */
margin-right: -10px;
      margin-bottom: 25px;
      
  }
  
  }
  
  @media (max-width: 798px) {
    .main-nav {
      height: 6rem;
      grid-template-columns: 1rem 2fr 1fr 1fr 1rem;
    }
    .dark-mode{
      height: 6rem;
      grid-template-columns: 1rem 2fr 1fr 1fr 1rem;
    }
    .logo,
    .social-media ul {
      height: 6rem;
    }
  
    .social-media {
      height: 6rem;
      display: flex;
      justify-self: end;
      align-items: center;
    }
  
    .social-media .hamburger-menu {
      display: block;
      font-size: 2.5rem;
    }
    .colorModeSwap{
      /* border: 1px solid rgb(72, 30, 226); */
     /* width: 45px; */
margin-right: -10px;
      margin-bottom: 25px;
      
  }
  }
  
  @media (max-width: 520px) {
    .main-nav {
      height: 6rem;
      grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
    }
    .dark-mode{
      height: 6rem;
      grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
    }
    .logo,
    .social-media ul {
      height: 6rem;
    }
  
    .logo h2 {
      font-size: 2rem;
    }
  
    .social-media {
      height: 6rem;
      display: flex;
      justify-self: end;
      align-items: center;
    }
  
    .social-media .hamburger-menu {
      display: block;
      font-size: 3rem;
    }
   
    .colorModeSwap{
        /* border: 1px solid rgb(72, 30, 226); */
       /* width: 45px; */
  margin-right: -10px;
        margin-bottom: 25px;
        
    }
  }
 